.loader {
	width: 48px;
	height: 48px;
	border: 3px solid black;
	border-radius: 50%;
	display: inline-block;
	position: relative;
	box-sizing: border-box;
	animation: rotation 1s linear infinite;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
.loader::after {
	content: "";
	box-sizing: border-box;
	position: absolute;
	left: 0;
	top: 0;
	background: white;
	border: 2px solid black;
	width: 16px;
	height: 16px;
	transform: translate(-50%, 50%);
	border-radius: 50%;
}

@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
