.form-wrapp {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: flex;
	flex-direction: column;
	padding: 30px;
	gap: 20px;
	box-shadow: 0px 0px 10px grey;
	border-radius: 10px;
}
.form-wrapp h2 {
	text-align: center;
}
.form-wrapp form {
	display: flex;
	flex-direction: column;
	gap: 20px;
}
